
import { computed, defineComponent, PropType } from "vue";
import { IonCard, IonCardContent } from "@ionic/vue";
import ApexChart from "vue3-apexcharts"

export default defineComponent({
  name: "LineChart",
  props: {
    series: {
      type: Array as PropType<any[]>,
      default: () => []
    },
    options: {
      type: Object as PropType<Record<string, any>>,
      default: () => ({})
    },
    height: {
      type: [String, Number],
      default: 560
    },
    placeholder: {
      type: String,
      default: "Loading data..."
    }
  },
  components: { IonCard, IonCardContent, ApexChart },
  emits: ["pointSelection"],
  setup(props, { emit }) {
    const chartOptions = computed(() => {
      const defaults: Record<string, any> = {
        chart: {
          id: "line-chart",
          type: 'line',
        },
        xaxis: {
          type: 'datetime',
          tickAmount: 2,
          labels: {
            show: true,
            rotate: -75,
          }
        },
        markers: {
          size: 8,
          hover: {
            sizeOffset: 3
          }
        },
        tooltip: {
          x: {
            format: 'dd/MMM/yyyy'
          }
        },
        title: {
          text: 'Line Chart',
          align: 'left'
        },
        noData: {
          text: props.placeholder,
          align: "center",
          verticalAlign: "middle",
          offsetX: 0,
          offsetY: 0,
          style: {
            color: "#a7a7a7",
            fontSize: "25px",
            fontFamily: "Helvetica",
          },
        },
      }

      for (const key in props.options) {
        defaults[key] = { ...defaults[key], ...props.options[key] };
      }
      
      return defaults;
    });

    const onPointSelection = (e: any, c: any, { dataPointIndex, seriesIndex }: any) => {
      console.log(e, c)
      emit("pointSelection", dataPointIndex, seriesIndex <= 0 ? 0 : seriesIndex)
    }
    return {
      chartOptions,
      onPointSelection,
    };
  },
});
